import React, {Component} from 'react';
import { Grid, Row, Col } from 'react-bootstrap';
import RecipeList from './components/recipelist';
import RecipeDetails from './components/recipedetails';
import AvailableIngredients from "./components/available_ingredients";

class App extends Component {

	constructor() {
		super();
		this.state = {
			didLoad: false,
			current_recipe: {},
			recipes:  [],
		}
	};

	componentDidMount() {

		fetch('http://alexhawley.info/api/recipe_react/get_recipes.php')
			.then(response => response.json())
			.then(responseData => {
				this.setState({
					didLoad: true,
					recipes: responseData.data,
					current_recipe: responseData.data[0]
				});
			})
	};

	handleClickRecipeLink = (id) => {

		let cur_recipes = this.state.recipes.filter(function(item){
			return item.id == id;
		});

		if (cur_recipes.length > 0) {
			this.setState({
				current_recipe: cur_recipes[0]
			});
		}
	};

	render() {
		return (this.state.didLoad)
		? <div className="container">
				<Grid>
					<div className="logo_image_holder">
						<img src="/images/chef_logo.png" className="logo_image" />
					</div>
					<div className="logo_text">Alex's Recipes</div>
					<hr />
					<div className="med_clear" ></div>
					<Row>
						<Col xs={12} md={7}>
							<RecipeList
								key="recipe_list"
								recipes={this.state.recipes}
								clickRecipe={this.handleClickRecipeLink}/>
						</Col>
						<Col xs={12} md={5}>
							<AvailableIngredients
								key="available_ingredients_component"
								use_available_ingredients={this.state.current_recipe.available_ingredients}
							/>
						</Col>
					</Row>
					<div className="xlarge_clear" ></div>

					<RecipeDetails
						key="recipe_details"
						recipe={this.state.current_recipe}
						/>
				</Grid>
			</div>
		: '';

	}
}

export default App;



